import { useFormik } from "formik";
import * as Yup from "yup";
import { useNavigate } from "react-router-dom";

export const ContactForm = () => {

    //Router
    const navigate = useNavigate();

    //Nodemailer

    // Formik logics
    const formik = useFormik({
        initialValues: {
            email: "",
            subject: "",
            message: "",
        },
        //Validate form
        validationSchema: Yup.object({
            email: Yup.string()
                .email("Please enter a valid email address")
                .required("An email address is required")
            
        }),

        //Submit button
        onSubmit: (values) => {
            navigate('/success', {state: {email: values.email}})

        }
    });

    return (
        <section className="bg-white dark:bg-gray-900">
            <div className="py-8 lg:py-32 px-4 mx-auto max-w-screen-md">
                <h2 className="mb-4 text-center font-bold text-5xl">Contact Us</h2>
                <p className="mb-8 lg:mb-16 text-center text-slate-500 text-lg sm:text-xl">Got a technical issue? Want to send feedback about a beta feature? Need details about our Business plan? Let us know.</p>
                <form onSubmit={formik.handleSubmit} className="space-y-8">
                    <div>
                        <label htmlFor="email" className={`block mb-2 text-sm font-medium text-gray-800 dark:text-gray-300 ${
                            formik.errors.email ? "text-red-400" : ""}`}
                        >
                        {formik.touched.email && formik.errors.email 
                            ? formik.errors.email 
                            : "Your email"}
                            </label>
                        <input  type="email" 
                                id="email"
                                value={formik.values.email}
                                onChange={formik.handleChange}
                                className="shadow-sm bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-primary-500 focus:border-primary-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-primary-500 dark:focus:border-primary-500 dark:shadow-sm-light" 
                                placeholder="name@localtrade.com" 
                                onBlur={formik.handleBlur}
                                required />
                    </div>
                    <div>
                        <label htmlFor="subject" className="block mb-2 text-sm font-medium text-gray-900 dark:text-gray-300">Subject</label>
                        <input  type="text" 
                                id="subject"
                                value={formik.values.subject}
                                onChange={formik.handleChange}
                                className="block p-3 w-full text-sm text-gray-900 bg-gray-50 rounded-lg border border-gray-300 shadow-sm focus:ring-primary-500 focus:border-primary-500 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-primary-500 dark:focus:border-primary-500 dark:shadow-sm-light" 
                                placeholder="Let us know how we can help you" required />
                    </div>
                    <div className="sm:col-span-2">
                        <label htmlFor="message" className="block mb-2 text-sm font-medium text-gray-900 dark:text-gray-400">Your message</label>
                        <textarea
                        id="message" 
                        rows={6}
                        value={formik.values.message}
                        onChange={formik.handleChange}
                        className="block p-2.5 w-full text-sm text-gray-900 bg-gray-50 rounded-lg shadow-sm border border-gray-300 focus:ring-primary-500 focus:border-primary-500 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-primary-500 dark:focus:border-primary-500" 
                        placeholder="Leave a comment..."></textarea>
                    </div>
                    <button type="submit" className="py-3 px-5 text-sm font-medium text-center text-white rounded-lg bg-green-700 sm:w-fit hover:bg-primary-800 focus:ring-4 focus:outline-none focus:ring-primary-300 dark:bg-primary-600 dark:hover:bg-primary-700 dark:focus:ring-primary-800">Send message</button>
                </form>
            </div>
        </section>
    )
}
